* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-optical-sizing: auto;
  justify-content: center;
  align-items: center;
  font-family: Alexandria, serif;
  display: flex;
}

#game {
  background-color: #e5e5e5;
  width: 1000px;
  height: 700px;
  position: relative;
}

#game header {
  background-color: #2ec4b6;
  width: 100%;
  padding: 26px 22px;
}

#game header h1 {
  font-size: 4rem;
}

#game nav {
  margin-top: 52px;
}

#game nav ul {
  list-style: none;
}

#game nav li {
  margin-bottom: 10px;
}

#game nav button {
  text-align: left;
  cursor: pointer;
  border: none;
  width: 204px;
  height: 62px;
  padding: 0 26px;
  font-size: 2rem;
  font-weight: normal;
  transition: background-color .2s ease-in;
}

#game nav button.primary {
  background-color: #ff9f1c;
}

#game nav button.primary:hover {
  background-color: #ed951c;
}

#game nav button.secondary {
  background-color: #c3c3c3;
}

#game nav button.secondary:hover {
  background-color: #b5b5b5;
}

#game-canvas {
  cursor: pointer;
  width: 659px;
  height: 564px;
  position: absolute;
  bottom: 32px;
  right: 67px;
}

#score {
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
}

#score > span {
  margin-right: 12px;
  font-size: 24px;
  font-weight: 300;
  display: block;
}

#score .score-wrapper {
  width: 204px;
  display: flex;
}

#score .score-badge {
  justify-content: space-evenly;
  align-items: center;
  width: 50%;
  height: 62px;
  display: flex;
}

#score .score-badge span {
  font-size: 24px;
  font-weight: 900;
}

#score .score-badge.score-user {
  background-color: #ffbc60;
}

#score .score-badge.score-user.is-playing {
  box-shadow: inset 0 0 0 8px #ed951c;
}

#score .score-badge.score-machine {
  background-color: #e71d36;
}

#score .score-badge.score-machine.is-playing {
  box-shadow: inset 0 0 0 8px #c41d32;
}

#match-end-modal {
  text-align: center;
  background-color: #00000070;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 410px;
  height: 187px;
  margin: auto;
  display: none;
  position: absolute;
  bottom: 215px;
  right: 191px;
  box-shadow: inset 0 120px #00000080;
}

#match-end-modal.is-match-ended {
  display: flex;
}

#match-end-modal h3 {
  color: #fff;
  margin-bottom: 16px;
  font-size: 48px;
  font-weight: 900;
}

#match-end-modal button {
  color: #000;
  cursor: pointer;
  background-color: #2ec4b6;
  border: none;
  width: 190px;
  height: 55px;
  font-size: 24px;
}
